<template>
  <div class="detail" id="notebook-list">
    <header>
      <el-button class="btn" type="text" @click.prevent="onCreate"><i class="iconfont icon-plus"></i> 新建笔记本</el-button>
    </header>
    <main>
      <div class="layout">
        <h3>笔记本列表({{ notebooks.length }})</h3>
        <div class="book-list">
          <router-link v-for="(notebook,index) in notebooks" :key="index" :to="`/note?notebookId=${notebook.id}`" class="notebook">
            <div>
              <span class="iconfont icon-notebook"></span>
              {{ notebook.title }}
              <span>{{ notebook.noteCounts }}</span>
              <span class="action" @click.stop.prevent="onEdit(notebook)">编辑</span>
              <span class="action" @click.stop.prevent="onDelete(notebook)">删除</span>
              <span class="date">{{ notebook.createdAtFriendly }}</span>
            </div>
          </router-link>
        </div>
      </div>
    </main>
  </div>
</template>

<script>
import Auth from '../apis/auth'
import Notebooks from '../apis/notebooks'
import {friendData} from "../helpers/util";
import {mapState, mapActions,mapGetters} from 'vuex';


export default {
  name: 'NotebooksList',
  data() {
    return {
    }
  },
  created() {
    this.checkLogin({ path: '/login' })
    this.getNotebooks()
  },
  computed:{
    ...mapGetters(['notebooks'])
  },
  methods: {
    ...mapActions([
      'getNotebooks',
      'addNotebook',
      'updateNotebook',
      'deleteNotebook',
      'checkLogin'
    ]),
    onEdit(notebook) {
      this.$prompt('修改新笔记本标题', '编辑', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputValue: notebook.title,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({ value }) => {
        this.updateNotebook({ notebookId: notebook.id, title: value })
      })
    },
    onDelete(notebook) {
      this.$confirm('你确定删除吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.deleteNotebook({ notebookId: notebook.id })
      })
    },
    onCreate() {  //懂了
      this.$prompt('输入新笔记本标题', '创建笔记本', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        inputPattern: /^.{1,30}$/,
        inputErrorMessage: '标题不能为空，且不超过30个字符'
      }).then(({ value }) => {
        this.addNotebook({ title: value })
      })
    },
  },
}
</script>

<style lang="less" scoped>
@import url(../assets/css/notebook-list.less);
</style>



